body {
  font-family: var(--mainFont);
  background-color: #0b0b18;
  color: var(--mainFontColor);
  font-size: 20px;
}

.App {
  max-inline-size: 1920px;
  min-inline-size: 375px;
  min-height: 100vh;
  padding: 25px 0 105px;
  /* padding: 27px clamp(20px, clamp(40px, 8.333vw - 3.75rem, 100px) ,100px) 105px; */
  margin: 0 auto;
  width: 100%;
  position: relative;
}

.background-circle {
  height: clamp(21.75rem, 15.972vw + 9.771rem, 28.938rem);
  width: clamp(43rem, 31.667vw + 19.25rem, 57.25rem);
  background-image: url(../../images/ring-from-header.png);
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  left: -381px;
  top: -146px;
  z-index: -1;
}

@media (max-width: 576px) {
  .App {
    padding-top: 15px;
  }
}