.logo {
  display: flex;
  gap: 20px;
  transition: opacity 0.3s linear;
}

.logo:hover {
  opacity: 0.6;
}

.header__logo {
  display: flex;
  align-items: center;
}

.logo-img {
  width: 43px;
}

.logo-texts {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 5px;
  color: var(--mainFontColor);
  font-variation-settings: 'wght' 400;
}

.logo-text {
  font-size: 16px;
  line-height: 16px;
}

.logo-text:first-child {
  font-family: var(--secondaryFont);
  font-size: 30px;
  font-variation-settings: 'wght' 900;
  line-height: 30px;
}
.accent-text {
  color: var(--accent-color);
}

@media (max-width: 768px) {
  .logo {
    gap: clamp(0.625rem, 5.208vw - 1.25rem, 1.25rem);
  }
  .logo-img {
    width: clamp(1.875rem, 6.771vw - 0.563rem, 2.688rem);
  }

  .logo-text:first-child {
    font-size: clamp(1.25rem, 5.208vw - 0.625rem, 1.875rem);
    line-height: clamp(1.25rem, 5.208vw - 0.625rem, 1.875rem);
  }

  .logo-text {
    font-size: clamp(0.813rem, 1.563vw + 0.25rem, 1rem);
    line-height: clamp(0.813rem, 1.563vw + 0.25rem, 1rem);
  }
}

@media (max-width: 576px) {
  .logo-img {
    height: 43px;
  }
}