.contacts {
  display: flex;
  align-items: center;
  gap: 15px;
}

.tel-icon {
  width: 16.667px;
  height: 30px;
  transition: opacity 0.3s linear;
}

.tel-icon:hover {
  opacity: 0.6;
}

.contacts__number {
  color: var(--mainFontColor);
  font-size: 30px;
  line-height: 30px;
  font-style: normal;
  font-variation-settings: "wght" 700;
  transition: opacity 0.3s linear;
}

.contacts__number:hover {
  opacity: 0.6;
}

@media (max-width: 960px) {
  .contacts__number {
    font-size: clamp(1.25rem, 5.208vw - 1.25rem, 1.875rem);
  }
}

@media (max-width: 768px) {
  .contacts__number {
    font-size: clamp(1.063rem, 1.563vw + 0.5rem, 1.25rem);
    line-height: clamp(1.063rem, 6.771vw - 1.375rem, 1.875rem);
  }
}

@media (max-width: 548px) {
  .contacts {
    display: none;
  }
}