.section-title {
  width: calc(100% - clamp(180px, 16.667vw - 1.25rem, 300px));
  margin: 0 auto;
  margin-block-end: clamp(92.35px, 3.194vw + 3.729rem, 121px);
}

.section-title__container {
  width: 100%;
}

.section-title__content {
  width: clamp(684px, 21.944vw + 26.292rem, 842px);
}

.section-title__title {
  font-size: clamp(50px, 1.389vw + 2.083rem, 60px);
  font-variation-settings: "wght" 900;
  line-height: clamp(60px, 1.389vw + 2.708rem, 70px);
  text-transform: uppercase;
  margin-bottom: 20px;
}

.section-title__subtitle {
  font-size: 25px;
  line-height: 120%;
  font-variation-settings: "wght" 400;
  margin-bottom: 25px;
}

/* #####################Стили карточки############################# */

.section-title__cards {
  display: flex;
  gap: clamp(15px, 2.083vw - 0.625rem, 30px);
  margin-bottom: 28px;
}

.section-title__card {
  display: flex;
  flex-direction: column;
  width: clamp(202px, 5.972vw + 8.083rem, 244px);
  min-height: 100%;
  gap: 10px;
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0px 0px 15px 0px rgba(255, 255, 255, 0.1) inset;
  padding: 15px 14px;
}

.section-title__card-header {
  display: flex;
  align-items: center;
  flex-grow: 1;
  gap: 20px;
}

.section-title__img {
  line-height: 0;
}

.section-title__card-img {
  display: flex;
  max-inline-size: 70px;
  width: 100%;
}

.section-title__card-title {
  font-size: 20px;
  font-variation-settings: "wght" 700;
  line-height: 125%;
}

.section-title__card-text {
  flex-grow: 1;
  color: #c0c1c8;
  font-size: 16px;
  font-variation-settings: "wght" 400;
  line-height: 156.25%;
}

.section-title__card-text_accent {
  font-variation-settings: "wght" 700;
}

/* #########################Стили подвала блока######################################## */

.section-title__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  padding: 15px 30px;
  border-radius: 100px;
  border: 3px dashed #626bcb;
  max-inline-size: 773px;
  width: 100%;
}

.section-title__footer-text {
  font-variation-settings: "wght" 700;
  line-height: 30px;
  text-align: center;
}

.section-title__messengers {
  display: flex;
  align-items: center;
  gap: 10px;
}

.section-title__messenger {
  padding: 10px 15px;
  border-radius: 100px;
  display: flex;
  gap: 10px;
  transition: opacity 0.3s linear;
}

.section-title__messenger:hover {
  opacity: 0.6;
}

.section-title__messenger-telegram {
  background-color: #34aadf;
}

.section-title__messenger-whatsapp {
  background-color: #00d95f;
}

.section-title__messenger-icon {
  width: 24px;
  height: 24px;
}

.section-title__messenger-text {
  font-size: 16px;
  font-variation-settings: "wght" 400;
  line-height: 156.25%;
}

@media (max-width: 1200px) {
  .section-title {
    width: calc(100% - clamp(60px, 50vw - 26.25rem, 180px));
    /* padding: 0 clamp(0px, 20.833vw - 12.5rem, 50px); */
    margin-block-end: clamp(92.35px, -8.333vw + 12rem, 112.35px);
  }

  .section-title__content {
    width: clamp(588px, 40vw + 12.75rem, 684px);
  }

  .section-title__header {
    width: clamp(344px, 127.083vw - 39.5rem, 100%);
  }

  .section-title__title {
    font-size: clamp(40px, 4.167vw, 50px);
    line-height: 120%;
  }

  .section-title__subtitle {
    font-size: 22px;
    line-height: 113.636%;
  }

  .section-title__card-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .section-title__card-title {
    font-size: 18px;
    line-height: 22px;
  }

  .section-title__card-text {
    font-size: 15px;
    line-height: 20px;
  }
}

@media (max-width: 960px) {
  .section-title {
    width: calc(100% - clamp(40px, 10.417vw - 2.5rem, 60px));
    margin-block-end: clamp(62px, 26.042vw - 8.625rem, 112.35px);
  }

  .section-title__subtitle {
    font-size: 20px;
  }

  .section-title__footer-text {
    font-size: clamp(17px, 1.563vw + 0.313rem, 20px);
    line-height: clamp(25px, 2.604vw + 0.313rem, 30px);
  }
}

@media (max-width: 768px) {
  .section-title {
    margin-block-end: clamp(62px, -10.417vw + 8.875rem, 82px);
  }

  .section-title__content {
    width: 100%;
  }

  .section-title__title {
    font-size: 35px;
    line-height: 45px;
  }

  .section-title__card {
    width: 100%;
  }

  .section-title__card-img {
    width: clamp(47px, 11.979vw - 1.375rem, 70px);
  }
}

@media (max-width: 686px) {
  .section-title__cards {
    flex-direction: column;
  }

  .section-title__card-header {
    flex-direction: row;
    align-items: center;
  }

  .section-title__footer {
    flex-direction: column;
    border-radius: 20px;
  }
}

@media (max-width: 576px) {
  .section-title__title {
    font-size: 30px;
    line-height: 40px;
  }

  .section-title__subtitle {
    font-size: 17px;
    line-height: 25px;
    width: 284px;
  }
}

@media (max-width: 376px) {
  .section-title__card {
    padding: 10px;
  }

  .section-title {
    margin-block-end: 87px;
  }
  .section-title__subtitle {
    width: 100%;
  }

  .section-title__card-title {
    font-size: 17px;
  }
}