.button-menu {
border: 1px solid var(--accent-color);
border-radius: 30px;
display: flex;
justify-content: center;
align-items: center;
gap: 15px;
padding: 15px 20px;
max-inline-size: 129px;
width: 100%;
}

.button-menu__img {
  display: flex;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: opacity 0.3s linear;
  width: 21px;
  height: 15px;
}

.button-menu__img:hover {
  opacity: 0.6;
}

.button-menu__text {
  border: none;
  background-color: transparent;
  color: var(--mainFontColor);
  line-height: 20px;
  cursor: pointer;
  transition: opacity 0.3s linear;
}

.button-menu__text:hover {
  opacity: 0.6;
}

@media (max-width: 700px) {
  .button-menu__text {
    display: none;
  }

  .button-menu {
    width: 50px;
    height: 50px;
    padding: 10px 15px;
    border-radius: 50%;
  }

  /* .button-menu__img {
    display: flex;
  } */
}