*,
*::after,
*::before,
h1 {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: currentColor;
}

li {
  list-style: none;
}

img {
  width: 100%;
  line-height: 0;
}

