.service__title {
  font-size: clamp(60px, 1.389vw + 2.708rem, 70px);
  font-variation-settings: "wght" 900;
  line-height: 80px;
  text-transform: uppercase;
  margin-block-end: 50px;
}

@media (max-width: 1200px) {
  .service__title {
    font-size: clamp(50px, 4.167vw + 0.625rem, 60px);
    line-height: clamp(60px, 8.333vw - 1.25rem, c80px);
    margin-block-end: 40px;
  }
}

@media (max-width: 376px) {
  .service__title {
    margin-block-end: 30px;
  }
}

/* ##################Ссылки на страницы##################### */
.service {
  padding: 0 clamp(0px, 6.944vw - 5.208rem, 50px);
}

.service__links {
  display: flex;
  align-items: start;
  align-self: stretch;
  gap: 2px;
}

.service__link {
  display: flex;
  align-items: center;
  text-align: center;
  padding: clamp(15px, 0.694vw + 0.417rem, 20px) 45px;
  font-size: clamp(25px, 0.694vw + 1.042rem, 30px);
  font-variation-settings: "wght" 700;
  line-height: clamp(25px, 0.694vw + 1.042rem, 30px);
  background: rgba(255, 255, 255, 0.20);
  color: #C0C1C8;
  border-radius: 7px 7px 0px 0px;
  min-height: 100px;
}

.service__link-active {
  background-color: #fff;
  color: #000;
}

@media (max-width: 960px) {
  .service__link {
    padding: 15px 43px;
    font-size: clamp(20px, 2.604vw, 25px);
    line-height: clamp(20px, 2.604vw, 25px);
  }
}

@media (max-width: 768px) {
  .service__link {
    padding-left: 37px;
    padding-right: 37px;
  }
}

@media (max-width: 576px) {
  
}

@media (max-width: 376px) {
  .service__link {
    font-size: 17px;
    line-height: 17px;
  }
}