.header__messengers {
display: flex;
align-items: center;
gap: 30px;
}

.header__messenger-whatsapp {
  display: block;
  width: 24px;
  height: 24px;
}

.header__messenger-telegram {
  display: block;
  width: 21px;
  height: 18px;
}

.header__messenger-icon {
  width: 100%;
  transition: opacity 0.3s linear;
}

.header__messenger-icon:hover {
  opacity: 0.6;
}

@media (max-width: 960px) {
  .header__messengers {
    gap: clamp(1.25rem, 5.208vw - 1.25rem, 1.875rem);
  }
}