.header {
  width: calc(100% - clamp(80px, 16.667vw - 7.5rem, 200px));
  margin: 0 auto clamp(68.5px, 4.236vw + 1.104rem ,99px);
}


.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-inline-size: 1690px;
  width: 100%;
}

.header__navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-inline-size: 467px;
  width: 100%;
}

.header__contacts {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-inline-size: 471px;
  width: 100%;
  align-items: center;
}

.contacts__tel-icon {
  display: flex;
  line-height: 30px;
}

@media (max-width: 1200px) {
  .header {
    width: calc(100% - clamp(60px, 8.333vw - 1.25rem, 80px));
  }

  .header__container {
    max-inline-size: 1120px;
  }

  .header__navigation {
    width: clamp(26.063rem, 20.833vw + 13.563rem, 29.188rem);
  }

  .header__contacts {
    width: clamp(26.063rem, 20.833vw + 13.563rem, 29.188rem);
  }
}

@media (max-width: 960px) {
  .header {
    width: calc(100% - clamp(40px, 10.417vw - 2.5rem, 60px));
  }

  .header__navigation {
    width: clamp(24.813rem, 10.417vw + 19.813rem, 26.063rem);
  }


  .header__contacts {
    width: clamp(18.938rem, 61.458vw - 10.563rem, 26.313rem);
  }
}

@media (max-width: 700px) {
  .header__navigation {
    gap: 20px;
    justify-content: flex-start;
  }
}

@media (max-width: 768px) {
  .header {
    margin-bottom: clamp(54px, 7.292vw + 0.75rem, 68.5px);
  }
  .header__navigation {
    width: clamp(14.813rem, 83.333vw - 15rem, 24.813rem);
  }


  .header__contacts {
    width: clamp(16.625rem, 19.271vw + 9.688rem, 18.938rem);
  }
}

@media (max-width: 576px) {
  .header {
    width: clamp(21rem, 100.5vw - 2.618rem, 33.563rem);
  }
  .header__navigation {
    min-width: 240px;
    flex-direction: row-reverse;
  }
}

@media (max-width: 548px) {
  .header__contacts {
    justify-content: end;
  }
}